html,
body {
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0.7))), url("../resources/restaurant.jpg");
    background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../resources/restaurant.jpg");
    background-image: -o-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../resources/restaurant.jpg");
    background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../resources/restaurant.jpg");
    background-color: black;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100%;
}

.s-alert-box {
    min-width: 250px;
}

.s-alert-close::before,
.s-alert-close::after {
    width: 2px;
}


